.header {
    top: 0;
    left: 0;
    position: sticky;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 38px;
    padding-right: 48px;
    padding-left: 48px;
    padding-top: 20px;
    padding-bottom: 20px;
  
    background-color: #1e1e1e;
    box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
      0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
  
    font-weight: 700;
    font-size: 28px;
    line-height: 1.5;
  }
  
  .navigation {
    display: flex;
    gap: 30px;
  }